import React from "react";
import { HashRouter as Router, Route, Link} from "react-router-dom";
import Home from "./component/parent/Home";
import About from "./component/parent/About";
import Mail from "./component/parent/Mail";

export default class App extends React.Component {
    constructor(props) {
        super(props);

        this.state = {};
    };

    render() {
        return (
            <div>
                <Router>
                    <div>
                        <Link to="/">首页</Link>
                        <Link to="/about" style={{margin: "5px 10px"}}>关于</Link>
                        <Link to="/call">联系</Link>
                    </div>
                    <div>
                        <Route exact={true} path="/" component={Home}/>
                        <Route path="/about" component={About}/>
                        <Route path="/call" component={Mail}/>
                    </div>
                </Router>
            </div>
        );
    };
};
