import React from "react";
import axios from "axios";
import fetchJsonp from "fetch-jsonp";

export default class About extends React.Component{
    constructor(props) {
        super(props);

        this.state = {
            axiosData: [],
            fetchData: [],
            domain: "http://www.phonegap100.com"
        };
    };

    getAxiosData = () => {
        var api = this.state.domain + "/appapi.php?a=getPortalList&catid=20";
        axios.get(api).then(response=>{
            // console.log(response);
            this.setState({
                axiosData: response.data.result
            });
        }).catch(error=>{
            console.log(error);
        });
    };

    getFetchData = () => {
        var api = this.state.domain + "/appapi.php?a=getPortalList&catid=20";
        fetchJsonp(api).then(response=>{
            return response.json();
        }).then(json=>{
            this.setState({
                fetchData: json.result
            });
        }).catch(error=>{
            console.log(error);
        });
    };

    render() {
        return (
            <div>
                <input type="button" value="获取axios的接口" onClick={this.getAxiosData}/>
                <ul>
                    {
                        this.state.axiosData.map((value, key)=>{
                            return (
                                <li key={key}>
                                    {value.title}
                                </li>
                            );
                        })
                    }
                </ul>
                <hr/>
                <input type="button" value="获取axios的接口" onClick={this.getFetchData}/>
                <ul>
                    {
                        this.state.fetchData.map((value, key)=>{
                            return (
                                <li key={key}>
                                    {value.aid} - {value.title}
                                </li>
                            );
                        })
                    }
                </ul>
            </div>
        );
    };
};


